import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events } from 'ionic-angular';

/**
 * Generated class for the CarrierTripPackageNumberInput page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-carrier-trip-package-number-input',
  templateUrl: 'carrier-trip-package-number-input.html',
})
export class CarrierTripPackageNumberInput {
  type:number;
  inputEvents:Events;
  title:string;
  constructor(public navCtrl: NavController, public navParams: NavParams, public events: Events) {
    this.inputEvents = events;
    this.type = navParams.get('type');
    if(this.type == 1) {
      this.title = "Pick up Package";
    } else if (this.type == 2) {
      this.title = "Drop off Package";
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CarrierTripPackageNumberInput');
  }

  public confirmPackageNumber() {
    if (this.type == 1) {
      this.inputEvents.publish('numberInput', "1");
    } else {
      this.inputEvents.publish('numberInput', "2");
    }
    this.navCtrl.pop();
  }
}



